import React from "react";
import BuildControl from "./BuildControl/BuildControl";
import classes from "./BuildControls.module.css";
const controls = [
  { label: "Salad", type: "salad" },
  { label: "Bacon", type: "bacon" },
  { label: "Cheese", type: "cheese" },
  { label: "Meat", type: "meat" },
];
const BuildControls = (props) => {
  return (
    <div className={classes.BuildControls}>
      <p>Current Price : <strong>{props.price.toFixed(2)}</strong></p>
      {controls.map((ctlr) => {
        return <BuildControl key={ctlr.label} label={ctlr.label}
        added={()=>props.ingredientAdded(ctlr.type)} 
        removed={()=>props.ingredientRemoved(ctlr.type)}
        disabled={props.disabled[ctlr.type]} />;
      })}
      <button  className={classes.OrderButton} disabled={!props.purchaseable} onClick={props.ordered}>{props.isAuth ?"ORDER NOW":"SIGN UP TO ORDER"}</button>
    </div>
  );
};
export default BuildControls;
